import React, { useEffect, useState } from 'react';
import API from '../../../services/api';

const Table = (data) => {
  return <div>{JSON.stringify(data)}</div>
}

const Submission = ({ checkeds, setCheckeds }) => {

  const [data, setData] = useState({});

  const getSubmission = async(pid) => {
    setData(await API.call('GET', `/submission?pid=${pid}`, {}));
  }

  useEffect(() => {
    getSubmission(1);
  }, [])

  const setup = (idx) => {
    setCheckeds(checkeds.map( (_, i) => i === idx ? 1 : 0 ))
    getSubmission(idx + 1);
  }

  return (
    <div className="submission-container">
      <div className="submission-controller">
        程式碼執行結果:
        {
          checkeds.map( (checked, idx) =>
            <span key={idx}> 
              <input
                type="checkbox"
                checked={checked}
                onChange={ () => setup(idx) }
              />
              <span>{`第${idx + 1}題 `}</span>
            </span>
          )
        }
      </div>
      <Table data={data} />
    </div>
  );
}

export default Submission;